import React from 'react';
import { Link } from 'gatsby';
import Image from '../image';
import './style.scss';

function Bio({ author, language = 'ko' }) {
  // author가 없거나 null인 경우 방어
  if (!author) {
    return (
        <div className="bio-wrapper">
          <div>작성자 정보를 불러올 수 없습니다.</div>
        </div>
    );
  }

  const { bio = {}, social = {}, name = 'Author' } = author;
  const { role = '', description = [], thumbnail = '' } = bio;

  return (
      <div className="bio-wrapper">
        <div className="bio">
          {thumbnail && (
              <div className="author-thumbnail">
                <Image src={thumbnail} alt="author-thumbnail" />
              </div>
          )}
          <div className="author-info">
            <div className="author-name">
              <span>@{name}</span>
              {role && <span className="author-role">({role})</span>}
            </div>
            <div className="author-introduction">
              {Array.isArray(description) && description.length > 0
                  ? description.join(' ')
                  : '소개가 없습니다.'
              }
            </div>
            <div className="author-socials">
              {social.github && (
                  <a href={social.github} target="_blank" rel="noopener noreferrer">
                    GitHub
                  </a>
              )}
              {social.linkedIn && (
                  <a href={social.linkedIn} target="_blank" rel="noopener noreferrer">
                    LinkedIn
                  </a>
              )}
              {social.email && (
                  <a href={`mailto:${social.email}`}>
                    Email
                  </a>
              )}
            </div>
          </div>
        </div>
      </div>
  );
}

export default Bio;